.info-container {
    flex: 0.7;
    padding: 0.9rem;
    justify-content: space-around;
    align-items: center;
    display: flex;
}

.profile-image-container {
    height: 160px;
    border-radius: 50%;
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-image-container img {
    height: 100%;
    width: 100%;
}

.details-container {
    height: auto;
    padding: 0.9rem;
    margin-left: 2rem;
    width: 40%;
}

.details-container h3{
    font-size: 0.9rem;
    color: #131313;
    margin-bottom: 0.5rem;
    font-weight: 500;
    margin-top: 0;
}

.details-container p {
    font-size: 0.9rem;
    font-weight: 500;
    color: gray;
    margin-top: 0;
    margin-bottom: 0.5rem;
}

.alt {
    margin: 0;
    color: white; 
    font-weight: 300; 
    font-size: 5rem; 
    text-transform: uppercase
}
