.verification-alert {
    height: auto;
    position: relative;
    width: 90%;
    background: white;
    max-width: 400px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    flex-direction: column;
}

.verification-alert h1 {
    text-align: center;
    font-size: 1rem;
}

.button-container {
    margin-top: 2rem;
}

.backdrop {
    position: fixed;
    background-color:rgb(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1000 !important;
    display: flex;
    align-items: center;
    justify-content: center;
    /* display: none; */
}

.qualification-form input,
.qualification-form select {
    height: 2.3rem;
    padding: 0.5rem;
    width: 100%;
    border: none;
    font-size: 0.7rem;
    color: gray;
    background-color: #d3d3d330;
    box-shadow: 0 1px 10px 0 lightgray;
    border-radius: 5px;
    overflow: auto;
}

.map-pin {
    margin-left: 1rem;
    height: 30px;
    justify-content: center;
    align-items: center;
    display: flex;
    width: 30px;
    background-color: #d3d3d330;
    cursor: pointer;
    box-shadow: 0 1px 10px 0 lightgrey;
}


.qualification-form textarea {
    height: 4rem;
}

.qualification-form label {
    margin-top: 1rem;
}

.qualification-form {
    display: flex;
    width: 100%;
    flex-direction: column;
    max-width: 600px;
    /* justify-content: center; */
    /* align-items: center; */
}

