.title {
    width: 100%;
    height: 3rem;
    margin-top: 0.9rem;
    display: flex;
    align-items: center;
    padding-left: 0.5rem;
    background-color: white;
}

.sub-container {
    height: auto;
    padding: 0.9rem;
    background-color: white;
    width: 98%;
    margin: auto;
    margin-top: 0.9rem;
    
    padding: 10px 10px 20px 10px;
    border: 1px solid #BFBFBF;
    box-shadow: 1px 0px 8px #aaaaaa;
}


.certificates-form {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    /* justify-content: center; */
    /* align-items: center; */
}

.certificates-form input,
.certificates-form select {
    height: 2.3rem;
    padding: 0.5rem;
    margin-top: 0.9rem;
    width: 100%;
    border: 1px solid black;
}

.certificates-form label {
    display: block;
}

.input-container {
    width: 100%;
    margin: 0.9rem;
    height: auto;
}

.description {
    width: 100%;
    height: 13rem;
    padding: 0.9rem;
}

.description textarea {
    width: 100%;
    height: 80%;
    margin-top: 0.9rem;
    border: 1px solid black;
}

.button-container {
    padding: 0.9rem;
    margin-top: 0.9rem;
}

.create-button,
.cancel-button {
    margin: 0.5rem;
    height: 2rem;
    width: 5rem;
    background-color: red;
    border: none;
    color: white;
    font-size: 1rem;
}

.create-button {
    background-color: green;
}

.file {
    margin-top: 0.5rem;
    /* padding: 0.9rem; */
}

.input-container .file {
    border: none;
}

textarea.invalid,
input.invalid {
    border: 1px solid red
}

@media(max-width: 600px) {
    .input-container {
        width: 100%;
    }

    form {
        margin: auto;
    }
}