.info-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    height: auto;
    box-shadow: 0px 1px 10px 0 lightgray;
    border-radius: 10px;
    background-color: white;
    padding: 0.9rem;
    /* border-radius: 5px; */
}

.logo-container {
    height: 160px;
    border-radius: 50%;
    width: 160px;
    display: flex;
    justify-content: center;
    align-self: center;
    align-items: center;
}

.logo-container img {
    height: 100%;
    width: 100%;
}
.basic-details-container {
    margin-top: 20px;
    height: auto;
    width: 100%;
}

.basic-details-block {
    display: flex;
    padding: 0.9rem;
    align-items: center;
    padding-left: 0;
    font-size: 1.4rem;
}

.basic-details-block p{
    font-size: 0.9rem;
    font-weight: 500;
    padding-left: 1rem;
    color: gray;
    margin-top: 0;
    margin-bottom: 0.5rem;
}

.info-container h3 {
    font-size: 0.9rem;
    color: #131313;
    margin-bottom: 0.5rem;
    font-weight: 500;
    margin-top: 0;
}

.about-container {
    margin-top: 20px;
    height: auto;
    width: 100%;
}

.about-container p {
    font-size: 0.9rem;
    font-weight: 400;
    color: gray;
    margin-top: 0;
    margin-bottom: 0.5rem;
}

.alt {
    margin: 0;
    color: white; 
    font-weight: 300; 
    font-size: 5rem; 
    text-transform: uppercase
}

@media(min-width: 800px) {
    .info-container {
        padding-left: 0;
        flex-direction: row;
        justify-content: space-around;
        max-height: 250px;
    }
    
    .basic-details-container {
        margin-top: 0;
        margin-left: 20px;
        border-right: 0.5px solid lightgray;
        flex: calc(1/3);
    }
    
    .logo-container {
        margin-top: 0;
        border-right: 0.5px solid lightgray;
        margin-left: 20px;
        flex: calc(1/3);
        max-width: 160px;
    }

    .about-container {
        margin-top: 0;
        padding-right: 1rem;
        flex: calc(1/3);
        overflow-y: scroll;
    }
}




