.sub-container {
    height: auto;
    padding: 0.9rem;
    background-color: white;
    width: 98%;
    margin: auto;
    margin-top: 0.9rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 10px 20px 10px;
    border: 1px solid #BFBFBF;
    box-shadow: 1px 0px 8px #aaaaaa;
}

.button {
    background-color: green;
    height: 40px;
    margin: 1rem;
    margin-bottom: 0;
    width: 130px;
}
