*, html, body {
    font-family: Roboto, sans-serif;
}

p {
    text-align: justify;
}

button:focus {
    outline: none!important
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.MuiFormControl-root textarea {
    box-shadow: 0 0 0 0;
}

::-webkit-scrollbar-thumb {
    background: lightgray;
}

::-webkit-scrollbar {
    width: 5px;
    height: 10px;
}
/* 
.MuiIconButton-label svg {
    display: none !important
} */

.MUIDataTableToolbarSelect-deleteIcon-1509 {
    display: none !important;
} 

#programs .MuiTableRow-root.MuiTableRow-head {
    display: none !important;
}

.MuiPaper-root table {
    margin-bottom: 1rem;
    margin-top: 1rem;
}

.MuiDrawer-paper {
    z-index: 100 !important;
}

.makeStyles-appBar-6,
.makeStyles-appBar-663{
    z-index: 501 !important;
}

.teachersList th {
    text-align: center;
}

/* button {
    background: green;
} */

button:disabled {
    background: lightgray !important;
}

.MuiList-root.MuiList-padding p {
    font-weight: bold;
}

.map-button {
    height: 100%;
    width: 100px;
    background-color: blue;
    color: white;
    display: flex;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

h2.MuiTypography-root {
    color: gray;
    font-weight: 300 !important;
} 

.addTeacher > div {
    height: 100%;
    width: 100%
}

.centre table th {
    width: 5px !important
}

.backdrop-common {
    position: fixed;
    background-color:rgb(0, 0, 0, 0.5);
    height: 100vh;
    width: 100vw;
    display: flex; 
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    z-index: 1000;
    /* display: none; */
}

button[title="Delete"] {
    display: none !important;
}
