.title {
    width: 100%;
    height: 3rem;
    margin-top: 0.9rem;
    display: flex;
    align-items: center;
    padding-left: 0.5rem;
    background-color: white;
}

.sub-container {
    height: auto;
    padding: 0.9rem;
    background-color: white;
    width: 98%;
    margin: auto;
    margin-top: 0.9rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 10px 20px 10px;
    border: 1px solid #BFBFBF;
    box-shadow: 1px 0px 8px #aaaaaa;
}
