.header {
    height: 4.2rem;
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 10;
    background-color: white;
    display: flex;
    justify-content: space-between;
    border-bottom: 0.2px solid lightgray;
    /* background-color: blue; */
}

.main-logo-toggle-container {
    padding-left: 0.9rem;
    height: 100%;
    width: 12rem;
    display: flex;
    flex-direction: row;
}

.main-logo-container {
    height: 100%;
    flex: 0.7;
    display: flex;
    align-items: center;
    /* padding: 8px; */
    width: 60px;
}

.main-logo {
    height: 80%;
}

.logo-container {
    height: 100%;
    padding-right: 0.9rem;
    width: 60px;
    display: flex;
    align-items: center;
}

.logo {
    height: 80%;
    width: 100%;
}

.toggle-logo-container {
    flex: 0.3;
    /* background-color: red; */
}
