.basic-info-container {
    display: flex;
    height: auto;
    margin-top: 10px;
    width: 100%;
    border-radius: 10px;
    padding: 0.9rem;
}

.detail-container {
    display: flex;
    align-items: center;
}

.job-container {
    background-color: white;
    height: 100%;
    box-shadow: 0 1px 20px 0 lightgray;
    border-radius: 10px;
    padding: 0.9rem;
}

.job-container h4 {
    margin-top: 0;
    font-size: 1rem;
    text-transform: uppercase;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid lightgray;
}

.job-container i {
    margin: 0.5rem 
}

.detail-container h3 {
    margin: 0;
    font-size: 0.8rem;
    font-weight: 500;
}

.detail-container h3 span {
    font-size: 0.8rem;
}


.interview-date {
    height: 2.5rem;
    display: flex;
    align-items: center;
    color: white;
    font-size: 0.9rem;
    margin-top: 0.9rem;
    border-radius: 5px;
    text-align: center;
    width: auto;
    max-width: 220px;
    background-color: #00B8B0;
}

.apply-link,
.delete {
    height: 2rem;
    width: auto;
    min-width: 6rem;
    padding: 0.5rem;
    display: flex;
    margin-top: 2rem;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    box-shadow: 0 0 2px 0px #0082B8;
    color: white;
    background-color: #0082B8;
}

.apply-link i {
    font-size: 1.3rem;
}

.apply-link a,
.apply-link a:hover {
    text-decoration: none;
    color: white;
    font-size: 1.2rem;
}

.jobs-container {
    height: auto;
    width: 95%;
    background-color: white;
    margin: auto;
    margin-top: 0.9rem;
    margin-bottom: 0.9rem;
}

.links {
    display: flex;
    height: auto;
    width: auto;
    margin-top: 0.9rem;
    height: auto;
}

.delete {
    background-color: red;
    box-shadow: 0 0 2px 0px red;
    margin-left: 0.9rem;
    font-size: 1.3rem;
}


.qualification p:first-of-type{
    color: gray;
    padding-left: 1rem;
    font-size: 1.5rem;
}
