.mobile-nav-container {
    /* position: fixed; */
    height: 100%;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    display: flex;
    /* z-index: 50; */
    background-color: white;
    /* transform: translateX(-450px); */
    /* transition: all 0.5s ease-out; */
}

.open-nav {
    transform: translateX(0);
}

.logo-container {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo-container img {
    height: 80%;
    width: 160px;
}

.existing-schools {
    padding: 2rem;
    padding-left: 4rem;
}

.existing-schools p {
    font-size: 0.9rem;
    font-weight: 300;
}

.content {
    max-height: 0;
    /* min-height: 0; */
    margin: 0;
    width: 100%;
    /* background-color: #F5F5F5; */
    transition: all 0.5s;
    overflow: hidden;
}

.content .link {
    width: 100%;
    height: 40px;
    display: flex;
    font-size: 0.9rem;
    font-weight: 500;
    align-items: center;
    padding-left: 1rem;
    text-transform: uppercase;
    /* text-align: center; */
    text-decoration: none;
    cursor: pointer;
}

.link {
    color: #6E6E6ECC
}

.support {
    font-weight: bold;
    font-size: 20px;
    color: #333;
    line-height: 26px;
    text-transform: none;
}

.content p {
    height: auto;
    font-weight: 300;
    font-size: 0.9rem;
    /* padding: 2rem; */
    /* margin-bottom: 0.3rem; */
    width: 100%;
}

.expand {
    /* min-height: 10rem;    */
    max-height: 50rem;
}

.toggler {
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 30px;
    width: 2rem;
    cursor: pointer;
    background-color: white;
    border-radius: 50%;
    margin-right: 2rem;
}

.toggle-bar {
    height: 3rem; 
    width: 100%;
    /* background-color: #E7E4CC; */
    margin-left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.main-link {
    padding-left: 1rem; 
    width: 100%; 
    height: 100%; 
    font-weight: 500;
    font-size: 1rem;
    color: #6E6E6ECC;
    display: flex; 
    align-items: center;
    cursor: pointer;
}

.main-link:hover,
.content .link:hover,
.contact:hover {
    background-color: #59b6ad24;
}

.toggle-bar h3 {
    margin: 0;
    width: auto;
    padding-left: 2rem;
    font-size: 1.2rem;
    font-weight: 400;
}

.content li {
    margin-top: 0.5rem;
}

/* @media(min-width: 840px) {
    .mobile-nav-container {
        display: none;
    }
} */

@media(max-width: 600px) {
    .mobile-nav-container {
        width: 70%;
    }

    .content .link {
        font-size: 0.65rem;
    }
}

@media(max-width: 320px) {
    .content .link {
        font-size: 0.55rem;
    }
}
