.header {
    height: 3.5rem;
    /* max-height: 4rem; */
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 0.9rem;
    padding-right: 0.9rem;
}

.header h3 {
    margin: 0;
    font-weight: 100;
    font-size: 1.5rem;
    border-bottom: 1.5px solid green;
}

.edit-link {
    height: 2rem;
    width: auto;
    min-width: 4rem;
    padding: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0082B8;
}

.header a {
    text-decoration: none;
    font-size: 1rem;
    color: white;
    font-weight: 50;
    text-align: center;
}
