.title {
    width: 100%;
    height: 3rem;
    margin-top: 0.9rem;
    display: flex;
    align-items: center;
    padding-left: 0.5rem;
    background-color: white;
}

.sub-container {
    height: auto;
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    align-items: center;
}

.fresher-container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fresher {
    border-radius: 5px;
    padding: 0.9rem;
    display: flex;
    flex-direction: column;
    background-color: white;
    align-items: center;
    justify-content: center;
    width: 50%;
    box-shadow: 0 1px 20px 0 lightgray;
    /* margin: auto; */
    height: auto;
}

.fresher p, label {
    text-align: center;
    font-weight: 500;
}

.is-fresher-button {
    height: 40px;
    display: flex;
    margin-top: 2rem;
    justify-content: center;
    align-items: center;
    width: 100px;
    background-color: #06b5ac;
    border-radius: 5px;
    cursor: pointer;
}

.is-fresher-button p {
    font-size: 1rem;
    margin: 0;
    color: white;
    color: white;
}

.fresher label {
    margin-left: 16px;
}

.empty {
    box-shadow: 0 1px 10x 0 lightgray;
    /* background-color: white; */
}