.title {
    width: 100%;
    height: 3rem;
    margin-top: 0.9rem;
    display: flex;
    align-items: center;
    padding-left: 0.5rem;
    background-color: white;
}

.sub-container {
    height: auto;
    padding: 0.9rem;
    background-color: white;
    width: 98%;
    margin: auto;
    margin-top: 0.9rem;
    padding: 10px 10px 20px 10px;
    box-shadow: 0px 1px 10px 0 lightgray;
}


.qualification-form {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    /* justify-content: center; */
    /* align-items: center; */
}

.qualification-form input,
.qualification-form select {
    height: 2.3rem;
    padding: 0.5rem;
    margin-top: 0.9rem;
    width: 100%;
    border: none;
    font-size: 0.7rem;
    color: gray;
    background-color: #d3d3d330;
    box-shadow: 0 1px 10px 0 lightgray;
    border-radius: 5px;
}

.qualification-form label {
    display: block;
    text-align: left;
}

.input-container {
    width: 23%;
    margin: 0.9rem;
    height: auto;
    min-width: 150px;
}

.button-container {
    padding: 0.9rem;
}

.create-button,
.cancel-button {
    margin: 0.5rem;
    height: 2rem;
    width: 5rem;
    background-color: red;
    border: none;
    color: white;
    font-size: 1rem;
}

.create-button {
    background-color: green;
}

.makeStyles-pageTitleContainer-222 button {
    height: 20px;
}

textarea.invalid,
input.invalid {
    border: 1px solid red
}

@media(max-width: 600px) {
    .input-container {
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }

    form {
        margin: auto;
    }
}