.title {
    width: 100%;
    height: 3rem;
    margin-top: 0.9rem;
    display: flex;
    align-items: center;
    padding-left: 0.5rem;
    background-color: white;
}

.sub-container {
    height: auto;
    padding: 0.9rem;
    background-color: white;
    width: 98%;
    margin: auto;
    margin-top: 0.9rem;
    
    padding: 10px 10px 20px 10px;
    box-shadow: 0px 1px 10px 0px lightgray;
}

.input-container {
    margin-top: 0.9rem;
    padding: 0.9rem;
}

.input-container label {
    display: block;
    text-align: left;
}

.input-container input {
    margin-top: 0.5rem;
}

.button-container {
    padding: 0.9rem;
}

.create-button,
.cancel-button {
    margin: 0.5rem;
    height: 2rem;
    width: 5rem;
    background-color: red;
    border: none;
    color: white;
    font-size: 1rem;
}

.create-button {
    background-color: green;
}

