.detail-container {
    display: flex;
    align-items: center;
}

.job-container {
    padding: 0.9rem;
    padding-top: 0;
}

.job-container h4 {
    margin-top: 0;
    text-transform: uppercase;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid lightgray;
}

.job-container i {
    margin: 0.5rem 
}

.detail-container h3 {
    margin: 0;
    font-size: 1.2rem;
    font-weight: 400;
}

.graduated-date {
    width: 15rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    color: white;
    font-size: 1rem;
    margin-top: 0.9rem;
    border-radius: 5px;
    text-align: center;
    background-color: #00B8B0;
}
.update-container {
    display: flex;
    margin-top: 2rem;
    height: auto;
}

.update-link {
    height: 2.1rem;
    width: 6rem;
    margin: 0.5rem;
    display: flex;
    justify-content: flex-start;
    padding-left: 0.1rem;
    align-items: center;
    background-color: #EDA500;
}

.update-link a,
.update-link i {
    text-decoration: none;
    color: black;
    font-size: 1.2rem;
}

.delete-link {
    background-color: red;
}

.delete-link a,
.delete-link i {
    color: white;
}

.qualification-container {
    width: 95%;
    background-color: white;
    border: 1px solid #BFBFBF;
    box-shadow: 1px 0px 8px #aaaaaa;
    padding: 0.9rem;
    margin-top: 1.5rem;
}
