.basic-info-container {
    display: flex;
    height: 15rem;
    width: 100%;
    border-radius: 10px;
    padding: 0.9rem;
    background-color: white;
    box-shadow: 0 1px 10px 0 lightgray;
}

.preference {
    position: fixed; 
    height: 40px; 
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 40px; 
    border-radius: 50%; 
    background: #06b5ac; 
    bottom: 5%; 
    z-index: 10;
    right: 5%;
    box-shadow: 0 1px 10px 0 lightgray;
}

.profile-strength-container {
    flex: 0.5;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background-color: green; */
}

.sub-container {
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 0;
}

.progressbarHeight {
    height: 100%;
    /* stroke: red;
    fill: red */
}

.CircularProgressbar-text {
    fill: green;
    font-size: 20px;
    text-anchor: middle;
}

.about-me,
.qualification,
.expericence {
    height: auto;
    width: 100%;
    padding-top: 1rem;
    font-weight: 300;
    border-bottom: 0.5px solid lightgray;
}

.about-me p:last-child {
    font-weight: 400;
    font-size: 1rem;
}

.qualification p:first-of-type,
.expericence p:first-of-type{
    color: gray;
    font-size: 1.5rem;
    font-weight: 300;
}
