.title {
    width: 100%;
    height: 3rem;
    margin-top: 0.9rem;
    display: flex;
    align-items: center;
    padding-left: 0.5rem;
    background-color: white;
}

.sub-container {
    height: auto;
    padding: 0.9rem;
    width: 98%;
    margin: auto;
    margin-top: 0.9rem;
    background-color: white;
    padding: 10px 10px 20px 10px;
    box-shadow: 0 1px 10px 0 lightgray;
}


.post-job-form {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    /* justify-content: center; */
    /* align-items: center; */
}

.post-job-form input, 
.input-container select {
    height: 2.3rem;
    padding: 0.5rem;
    margin-top: 0.9rem;
    width: 100%;
    border: none;
    font-size: 0.7rem;
    color: gray;
    background-color: #d3d3d330;
    box-shadow: 0 1px 10px 0 lightgray;
    border-radius: 5px;
}

.post-job-form label {
    display: block;
    text-align: left;
}

.input-container {
    width: 46%;
    margin: 0.9rem;
    height: auto;
}

.button-container {
    padding: 0.9rem;
}

.create-button,
.cancel-button {
    margin: 0.5rem;
    height: 2rem;
    width: 5rem;
    background-color: red;
    border: none;
    color: white;
    font-size: 1rem;
}

.create-button {
    background-color: green;
}

.description {
    width: 100%;
    height: 13rem;
    padding: 0.9rem;
}

.description textarea {
    width: 100%;
    height: 80%;
    margin-top: 0.9rem;
}
