.nav-bar {
    height: 100%;
    width: 20%;
    margin-left: 0;
    /* z-index: 2; */
    position: fixed;
    left: 0;
    background-color: #00B8AF;
}

.nav-bar ul {
    margin: 0;
    width: 100%;
    padding: 0;
    height: auto;
    display: flex;
    flex-direction: column;
    list-style: none;
}

.nav-bar li {
    height: 3rem;
    flex: calc(1/10);
    width: 100%;
    display: flex;
    justify-content: center;
    color: white;
    align-items: center;
}



.nav-bar i {
    flex: 0.2;
}

.nav-bar a,
.nav-bar i {
    color: white ;
    text-decoration: none;
    display: flex;
    padding-left: 0.2rem;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 100%;
    font-size: 1rem;
    font-weight: 100;
}

.nav-bar a:hover {
    background-color: #0082B8;
}

.title {
    flex: 0.8;
    font-size: 0.9rem;
    /* justify-self: flex-start; */
    text-align: left;
}