.detail-container {
    display: flex;
    align-items: center;
}

.job-container {
    padding: 0.9rem;
    overflow-y: scroll;
    height: 70%;
    padding-top: 0;
}

.job-container h4 {
    margin-top: 0;
    text-transform: uppercase;
    font-size: 1rem;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid lightgray;
}

.job-container i {
    margin: 0.5rem 
}

.job-container p {
    margin: 0;
    color: gray;
    font-size: 0.9rem;
    font-weight: 400;
}

.job-container p strong {
    color: #131313
}

.graduated-date {
    width: 15rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    color: white;
    font-size: 1rem;
    margin-top: 0.9rem;
    border-radius: 5px;
    text-align: center;
    background-color: #00B8B0;
}
.update-container {
    display: flex;
    position: absolute;
    height: auto;
    bottom: 5%;
}

.update-link {
    height: 2.1rem;
    width: 6rem;
    margin: 0.5rem;
    display: flex;
    justify-content: flex-start;
    padding-left: 0.1rem;
    align-items: center;
    background-color: #EDA500;
}

.update-link a,
.update-link i {
    text-decoration: none;
    color: black;
    font-size: 1.2rem;
}

.delete-link {
    background-color: red;
}

.delete-link a,
.delete-link i {
    color: white;
}

.qualification-container {
    width: 100%;
    background-color: white;
    box-shadow: 0 1px 10px 0 lightgray;
    padding: 0.9rem;
    border-radius: 10px;
    height: 15rem;
    margin-bottom: 1.2rem;
    position: relative;
}
